body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
      'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
      sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}
  
code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
      monospace;
}
.app__mobile-message {
    display: none;
    background: red;
}
@media screen and (max-width: 500px) {
    .app__mobile-message {
        padding-top: 200px;
        text-align: center;
        font-size: 1.2rem;
        display: block;
        color: black;
    }
    .app-content {
        display: none;
    }
}
.loader {
    background: rgb(18.6, 28.22, 33.4);
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.loader__logo-wrapper {
    position: relative;
}

.loader__logo-wrapper::after {
    content: "";
    position: absolute;
    left: -100px;
    bottom: 0;
    top: 0;
    width: 100px;
    background: linear-gradient( 90deg, rgba(18.6, 28.22, 33.4, .5) 0, rgba(18.6, 28.22, 33.4, .5) 33.33%, rgba(18.6, 28.22, 33.4, 0) 44.1%, rgba(18.6, 28.22, 33.4, 0) 55.8%, rgba(18.6, 28.22, 33.4, .5) 66.66%, rgba(18.6, 28.22, 33.4, .5));
    -webkit-animation: glisten 2s ease-in 0.8s infinite;
            animation: glisten 2s ease-in 0.8s infinite;
}

.loader__logo {
    z-index: 3;
    fill: rgb(108, 113, 117);
    width: 60px;
    height: 60px;
}

.loader__progress {
    width: 200px;
    max-width: 400px;
    height: 4px;
    width: 90%;
    margin: 20px auto 30px;
    background: rgb(42, 47, 50);
    position: relative;
}

.loader__progress::after {
    content: "";
    position: absolute;
    height: 100%;
    background: rgb(12, 137, 124);
    width: 0%;
    transition: width 2s linear;
}

.loader__progress--done::after {
    width: 100%;
}

.loader__title {
    color: rgba(241, 241, 242, 0.88);
    font-size: 1.1rem;
    font-weight: 500;
    margin-bottom: 10px;
}

.loader__desc {
    color: rgba(241, 241, 242, 0.45);
    font-size: 0.85rem;
}

.loader__icon {
    margin-right: 5px;
}

@-webkit-keyframes glisten {
    from {
        -webkit-transform: translateX(0);
                transform: translateX(0);
    }
    to {
        -webkit-transform: translateX(150px);
                transform: translateX(150px);
    }
}

@keyframes glisten {
    from {
        -webkit-transform: translateX(0);
                transform: translateX(0);
    }
    to {
        -webkit-transform: translateX(150px);
                transform: translateX(150px);
    }
}
